body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body {
  margin: 0;
  padding: 0;
}
.navbar {
  font-size: 10rem;
}
.container1 {
  background-color: rgba(0, 0, 0, 0.847);
  margin-bottom: 2%;
  display: flex;
  width: 100%;
}
.movie {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navigation {
  background-color: red;
  width: 100%;
}

.hero {
  margin-right: 1%;
  margin-left: 1%;
  margin-bottom: 3%;
  flex-wrap: wrap;
  display: flex;
}
.card {
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}
.card:hover {
  z-index: 100;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.card-body {
  background-color: rgba(216, 187, 187, 0.541);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0%;
}
.card-title {
  align-self: center;
}
.btn-flat {
  background-color: rgb(41, 118, 160);
  color: white;
}
#rating {
  border: 2px solid black;
  float: right;
}
.hehe {
  /*for same card height*/
  height: 98%;
}

.reacte {
  display: flex;
}

@media only screen and (min-device-width: 3rem) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) and (orientation: portrait) {
  .modal-body {
    flex-direction: column !important;
  }
}
li {
  display: inline-block;
}

.menu {
  justify-content: center;
  margin-bottom: 1%;
  margin-top: 1%;
  grid-gap: 10px;
  gap: 10px;
}
.list-group-item {
  cursor: pointer;
  transition: background-color 50ms ease-out 1ms;
}

.list-group-item:hover {
  background-color: #ea7676;
}

